import React from 'react'
import BannerCanvas from './bannerCanvas'
import anime from 'animejs/lib/anime.es.js'

export default class BannerBg extends React.PureComponent {
  constructor(props) {
    super(props)
    this.bgCanvas = null
    this.bgImg = null
    this.canvas = null
    this.canvasStart = false
    this.anime = null
  }

  componentDidMount() {
    this.canvas = new BannerCanvas({
      target: this.bgCanvas,
      vertexCount: 7000,
      vertexSize: 3,
      oceanWidth: 204,
      oceanHeight: -80,
      gridSize: 32,
      waveSize: 16,
      perspective: 100
    })
  }

  componentDidUpdate(prevProps) {
    if (!prevProps.isShow && this.props.isShow) {
      this.start()
    } else if (prevProps.isShow && !this.props.isShow) {
      this.destroy()
    }
  }

  start = () => {
    this.canvas.playCanvas()
    // window.dispatchEvent(new CustomEvent('bannerPlay'))
    this.anime = anime({
      targets: this.bgCanvas,
      opacity: [0, 1],
      duration: 1000,
      easing: 'easeInSine',
      complete: () => {
        anime.remove(this.bgCanvas)
        this.anime = null
      }
    })
  }

  destroy = () => {
    if (this.anime !== null) {
      this.anime.pause()
      this.anime = null
    }
    this.anime = anime({
      targets: this.bgCanvas,
      opacity: 0,
      duration: 1000,
      easing: 'easeInSine',
      complete: () => {
        anime.remove(this.bgCanvas)
        this.canvas.pauseCanvas()
        // window.dispatchEvent(new CustomEvent('bannerPause'))
        // this.bgCanvas.querySelector('canvas').remove()
      }
    })
  }

  render() {
    return <div className='bg-canvas' ref={node => (this.bgCanvas = node)} />
    // !this.isIphone ? (
    // ) : (
    //   <div className='bg-canvas'>
    //     <div
    //       className='bg-img'
    //       style={{
    //         backgroundImage: `url(${process.env.PUBLIC_URL}/bg-temp.png)`
    //       }}
    //       ref={node => (this.bgImg = node)}
    //     />
    //   </div>
    // )
  }
}
