import React from 'react'
import { ViewportBlock } from './view/viewport'
import Swipe from 'react-easy-swipe'
import Banner from './view/banner'
import Concept from './view/concept'
import Service from './view/service'
import Project from './view/project'
import About from './view/about'
import Contact from './view/contact'
import Cursor from './view/cursor'
import ControlArrow from './view/controller'
import MobileDetect from 'mobile-detect'

const ViewBlockBanner = ViewportBlock(Banner)
const ViewBlockConcept = ViewportBlock(Concept)
const ViewBlockService = ViewportBlock(Service)
const ViewBlockProject = ViewportBlock(Project)
const ViewBlockAbout = ViewportBlock(About)

export default class LayoutTemplate extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isLogoFinish: false,
      canScroll: false,
      controlShow: false,
      currentPage: 0,
      nextPage: 0
    }
    this.totalPageIndex = 4
    this.wrapper = null
    this.isMobile = new MobileDetect(window.navigator.userAgent).mobile()
    // console.log(this.isMobile)
  }

  componentDidMount() {
    window.addEventListener('mousewheel', this.handleScroll)
    this.initEffect()
    if (this.isMobile) {
      window.addEventListener('touchmove', this.preventTouch)
    }
  }

  preventTouch = event => {
    event.preventDefault()
  }

  initEffect = () => {
    document.documentElement.className = 'js'
    this.supportsCssVars() ||
      alert('Please view this in a modern browser that supports CSS Variables.')
    const script = document.createElement('script')
    script.src = 'sketch.js'
    script.async = true
    document.body.appendChild(script)
  }

  supportsCssVars = () => {
    let detectSupport
    let css = document.createElement('style')
    return (
      (css.innerHTML = 'root: { --tmp-var: bold; }'),
      document.head.appendChild(css),
      (detectSupport = !!(
        window.CSS &&
        window.CSS.supports &&
        window.CSS.supports('font-weight', 'var(--tmp-var)')
      )),
      css.parentNode.removeChild(css),
      detectSupport
    )
  }

  handleScroll = event => {
    if (this.state.isLogoFinish && this.state.canScroll) {
      if (event.deltaY > 0) {
        if (this.state.currentPage < this.totalPageIndex) {
          this.setState({
            nextPage: this.state.currentPage + 1
          })
        }
      } else {
        if (this.state.currentPage > 0) {
          this.setState({
            nextPage: this.state.currentPage - 1
          })
        }
      }
      if (this.state.currentPage !== 0) {
        this.wrapper.classList.add('scrolled')
      } else {
        this.wrapper.classList.remove('scrolled')
      }
    }
  }

  handleSwipeUp = () => {
    if (this.state.isLogoFinish && this.state.canScroll) {
      if (this.state.currentPage < this.totalPageIndex) {
        this.setState({
          nextPage: this.state.currentPage + 1
        })
      }
      if (this.state.currentPage !== 0) {
        this.wrapper.classList.add('scrolled')
      } else {
        this.wrapper.classList.remove('scrolled')
      }
    }
  }

  handleSwipeDown = () => {
    if (this.state.isLogoFinish && this.state.canScroll) {
      if (this.state.currentPage > 0) {
        this.setState({
          nextPage: this.state.currentPage - 1
        })
      }
      if (this.state.currentPage !== 0) {
        this.wrapper.classList.add('scrolled')
      } else {
        this.wrapper.classList.remove('scrolled')
      }
    }
  }

  handleTurn = down => {
    if (this.state.isLogoFinish && this.state.canScroll) {
      if (down) {
        if (this.state.currentPage < this.totalPageIndex) {
          this.setState({
            nextPage: this.state.currentPage + 1
          })
        }
      } else {
        if (this.state.currentPage > 0) {
          this.setState({
            nextPage: this.state.currentPage - 1
          })
        }
      }
      if (this.state.currentPage !== 0) {
        this.wrapper.classList.add('scrolled')
      } else {
        this.wrapper.classList.remove('scrolled')
      }
    }
  }

  handleJump = next => {
    if (this.state.isLogoFinish && this.state.canScroll) {
      this.setState({
        nextPage: next
      })
      if (this.state.currentPage !== 0) {
        this.wrapper.classList.add('scrolled')
      } else {
        this.wrapper.classList.remove('scrolled')
      }
    }
  }

  changeLogoFinish = () => {
    this.setState({ isLogoFinish: true, canScroll: true })
  }

  changeControl = () => {
    this.setState({
      controlShow: true
    })
  }

  changeDisableScroll = () => {
    this.setState({
      canScroll: false
    })
  }

  changeEnableScroll = () => {
    this.setState({
      canScroll: true
    })
  }

  changeCurrentPage = page => {
    this.setState({
      currentPage: page,
      canScroll: true
    })
  }

  changeBannerBgShow = value => {
    this.setState({
      bannerBgShow: value
    })
  }

  render() {
    return (
      <Swipe
        onSwipeUp={this.handleSwipeUp}
        onSwipeDown={this.handleSwipeDown}
        tolerance={50}
      >
        <div className='wrapper' ref={node => (this.wrapper = node)}>
          <ViewBlockBanner
            currentPage={this.state.currentPage}
            nextPage={this.state.nextPage}
            onLogoFinish={this.changeLogoFinish}
            onControl={this.changeControl}
            onDisableScroll={this.changeDisableScroll}
            onChangePage={this.changeCurrentPage}
          />
          <ViewBlockConcept
            currentPage={this.state.currentPage}
            nextPage={this.state.nextPage}
            onDisableScroll={this.changeDisableScroll}
            onChangePage={this.changeCurrentPage}
          />
          <ViewBlockService
            currentPage={this.state.currentPage}
            nextPage={this.state.nextPage}
            onDisableScroll={this.changeDisableScroll}
            onChangePage={this.changeCurrentPage}
          />
          <ViewBlockProject
            currentPage={this.state.currentPage}
            nextPage={this.state.nextPage}
            onDisableScroll={this.changeDisableScroll}
            onChangePage={this.changeCurrentPage}
          />
          <ViewBlockAbout
            currentPage={this.state.currentPage}
            nextPage={this.state.nextPage}
            onDisableScroll={this.changeDisableScroll}
            onChangePage={this.changeCurrentPage}
          />
          <Contact
            isShow={this.state.controlShow}
            isScroll={this.state.isScroll}
            onDisableScroll={this.changeDisableScroll}
            onEnableScroll={this.changeEnableScroll}
          />
          <ControlArrow
            isShow={this.state.controlShow}
            current={this.state.nextPage}
            onTurn={this.handleTurn}
            onJump={this.handleJump}
          />
          {!this.isMobile ? (
            <Cursor isShow={this.state.currentPage !== 0} />
          ) : null}
        </div>
        <div className='landscape-alert'>
          <span>為維持良好使用體驗，請將瀏覽方向調整為直向閱覽</span>
        </div>
      </Swipe>
    )
  }
}
