import React from 'react'
import LogoSvg from './logo'
import BannerBg from './bannerBg'
import anime from 'animejs/lib/anime.es.js'
import '../styles/sass/banner.sass'

export default class Banner extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      init: false,
      bannerBgShow: false
    }
    this.title = null
    this.slogan = null
    this.anime = null
  }

  componentDidMount() {
    this.splitText()
    this.sceneIn()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.currentPage !== 0 && this.props.currentPage === 0) {
      this.sceneIn()
    } else if (prevProps.nextPage === 0 && this.props.nextPage !== 0) {
      this.sceneOut()
    }
  }

  splitText = () => {
    const content = this.title.textContent
    this.title.textContent = ''
    let titleHtml = ''
    const contentSort = []
    content.split('').forEach((char, index) => {
      contentSort.push(index)
      titleHtml += `<span class="banner-title-span" data-sort="${index}">${char}</span>`
    })
    this.title.innerHTML = titleHtml
  }

  sceneIn = () => {
    const object = {
      opacity: 0,
      blur: 5,
      scale: 1.2,
      space: 40
    }
    this.anime = anime
      .timeline({ easing: 'easeInOutSine' })
      .add({
        targets: this.title,
        opacity: [0, 1],
        scale: [0, 1],
        duration: 1,
        delay: !this.state.init ? 5200 : 1200
      })
      .add({
        targets: this.title.querySelectorAll('span'),
        opacity: [0, 1],
        duration: 1000,
        easing: 'easeInOutSine',
        delay: () => anime.random(0, 1000),
        begin: () => {
          this.props.onControl()
          if (!this.state.init) {
            this.setState({ init: true })
          }
        }
      })
      .add({
        targets: object,
        opacity: [0, 1],
        blur: [5, 0],
        scale: [1.2, 1],
        space: [40, 10],
        easing: 'easeOutQuad',
        update: () => {
          this.slogan.style.opacity = object.opacity
          this.slogan.style.filter = `blur(${object.blur}px)`
          this.slogan.style.transform = `scale(${object.scale})`
          this.slogan.style.letterSpacing = `${object.space}px`
          this.slogan.style.paddingLeft = `${object.space}px`
        },
        begin: () => {
          this.setState({ bannerBgShow: true })
        },
        complete: () => {
          anime.remove(
            this.title,
            this.title.querySelectorAll('span'),
            this.slogan,
            this.bgTemp
          )
          this.anime = null
        }
      }, '-=1000')
  }

  sceneOut = () => {
    this.setState({
      init: true,
      bannerBgShow: false
    })
    this.props.onControl()
    this.props.onDisableScroll()
    if (this.anime !== null) {
      this.anime.pause()
      this.anime = null
    }
    this.anime = anime
      .timeline({ easing: 'easeInSine' })
      .add({
        targets: this.title,
        opacity: 0,
        scale: 0.75,
        duration: 1000
      })
      .add(
        {
          targets: this.slogan,
          opacity: 0,
          scale: 0.5,
          duration: 1000,
          complete: () => {
            anime.remove(this.bgTemp, this.title, this.slogan)
            this.anime = null
            setTimeout(() => {
              this.props.onChangePage(this.props.nextPage)
            }, 100)
          }
        },
        '-=900'
      )
  }

  render() {
    return (
      <div>
        <LogoSvg
          currentPage={this.props.currentPage}
          onFinish={this.props.onLogoFinish}
        />
        <div
          id='banner'
          className={`full-section ${
            this.props.currentPage === 0 ? 'current' : ''
          }`}
        >
          <BannerBg isShow={this.state.bannerBgShow} />
          <div className='text'>
            <h2 className='banner-title en' ref={node => (this.title = node)}>
              Design Your Future <br />
              Coding Your Dream
            </h2>
            <p className='banner-slogan' ref={node => (this.slogan = node)}>
              為您量身規劃『專屬網站』
            </p>
          </div>
          {/* {this.state.textShow && this.state.initIn ? (
            <div
              className={`text ${this.state.textFinished ? 'finished' : ''}`}
            >
              <BannerText
                nextPage={this.props.nextPage}
                onChangeTextFinish={this.handleTextFinish}
                onBannerBg={this.props.onBannerBg}
              />
            </div>
          ) : null} */}
        </div>
      </div>
    )
  }
}
