import React from 'react'
import ProjectBg from './projectBg'
import anime from 'animejs/lib/anime.es.js'
import '../styles/sass/project.sass'

export default class Project extends React.PureComponent {
  constructor(props) {
    super(props)
    this.title = null
    this.desc = null
    this.list = null
    this.buttons = []
    this.anime = null
  }

  componentDidMount() {
    this.splitText()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.currentPage !== 3 && this.props.currentPage === 3) {
      this.initEffect()
      this.sceneIn()
    } else if (prevProps.nextPage === 3 && this.props.nextPage !== 3) {
      this.sceneOut()
    }
  }

  splitText = () => {
    const titleStr = this.title.textContent
    let titleHtml = ''
    for (let i = 0; i < titleStr.length; i++) {
      titleHtml += `<span class="letter">${titleStr[i]}</span>`
    }
    this.title.innerHTML = titleHtml
  }

  initEffect = () => {
    if (document.getElementById('script-demo') === null) {
      const script2 = document.createElement('script')
      script2.id = 'script-demo'
      script2.src = 'demo3.js'
      script2.async = true
      document.body.appendChild(script2)
    }
  }

  sceneIn = () => {
    this.anime = anime
      .timeline({ easing: 'easeInSine' })
      .add({
        targets: this.title.querySelectorAll('.letter'),
        opacity: [0, 1],
        translateX: [() => `${Math.random() - 0.5 < 0 ? -10 : 10}px`, 0],
        duration: anime.random(800, 2000),
        delay: (e, i) => {
          return 200 + i * 100
        }
      })
      .add(
        {
          targets: this.desc,
          translateX: [50, 0],
          opacity: [0, 1],
          duration: 800
        },
        '-=1000'
      )
      .add(
        {
          targets: this.buttons,
          translateX: [50, 0],
          opacity: [0, 1],
          duration: 1000,
          delay: (e, i) => {
            return i * 120
          },
          complete: () => {
            this.list.classList.remove('not-hover')
            anime.remove(
              this.title.querySelectorAll('.letter'),
              this.desc,
              this.buttons
            )
            this.anime = null
          }
        },
        '-=800'
      )
  }

  sceneOut = () => {
    this.props.onDisableScroll()
    if (this.anime !== null) {
      this.anime.pause()
      this.anime = null
    }
    this.list.dispatchEvent(new Event('mouseleave'))
    this.list.classList.add('not-hover')
    this.anime = anime
      .timeline({ easing: 'easeInOutSine' })
      .add({
        targets: this.buttons,
        translateX: 50,
        opacity: 0,
        duration: 1000
      })
      .add(
        {
          targets: [this.desc, this.title.querySelectorAll('.letter')],
          opacity: 0,
          translateX: 0,
          duration: 1000,
          complete: () => {
            anime.remove(
              this.buttons,
              this.desc,
              this.title.querySelectorAll('.letter')
            )
            this.anime = null
            setTimeout(() => {
              this.props.onChangePage(this.props.nextPage)
            }, 100)
          }
        },
        '-=900'
      )
  }

  render() {
    return (
      <section
        id='project'
        className={`full-section ${
          this.props.currentPage === 3 ? 'current' : ''
        }`}
      >
        <div className='title-wrap'>
          <h2 className='tit en d-text' ref={node => (this.title = node)}>
            Project
          </h2>
          <div className='desc' ref={node => (this.desc = node)}>
            以前，我們幫助公司成就許多專案
            <br />
            現在，我們將帶著這些經驗來為您服務
          </div>
        </div>
        <nav
          id='project-list'
          className='list not-hover'
          ref={node => (this.list = node)}
        >
          <button
            className='project-name-btn'
            data-index={1}
            ref={node => (this.buttons[0] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/work_sonopro.html`)}
          >
            SONOPRO
          </button>
          <button
            className='project-name-btn'
            data-index={2}
            ref={node => (this.buttons[1] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/work_wutau.html`)}
          >
            WU TAU
          </button>
          <button
            className='project-name-btn'
            data-index={3}
            ref={node => (this.buttons[2] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/work_shinpuyuan.html`)}
          >
            SHIN PU YUAN
          </button>
          <button
            className='project-name-btn'
            data-index={4}
            ref={node => (this.buttons[3] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/work_viscovery.html`)}
          >
            VISCOVERY
          </button>
          <button
            className='project-name-btn'
            data-index={5}
            ref={node => (this.buttons[4] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/work_pica.html`)}
          >
            PICA PICA
          </button>
          <button
            className='project-name-btn'
            data-index={6}
            ref={node => (this.buttons[5] = node)}
            onClick={()=> window.open(`${process.env.PUBLIC_URL}/webTemplate/index.html`)}
          >
            WEB DEMO
          </button>
        </nav>
        <ProjectBg />
      </section>
    )
  }
}
