export default class BannerCanvas {
  constructor(options) {
    this.container = options.target

    this.vertexCount = options.vertexCount || 5000
    this.vertexSize = options.vertexSize || 3
    this.oceanWidth = options.oceanWidth || 204
    this.oceanHeight = options.oceanHeight || -20
    this.gridSize = options.gridSize || 32
    this.waveSize = options.waveSize || 16
    this.perspective = options.perspective || 200

    this.depth = (this.vertexCount / this.oceanWidth) * this.gridSize
    this.frame = 0
    this.play = false
    this.sin = Math.sin
    this.cos = Math.cos
    this.PI = Math.PI

    this.c = null
    this.postctx = null
    this.canvas = null
    this.vertices = []

    this.generate()
  }

  generate = () => {
    // Generating canvas
    this.c = document.createElement('canvas').getContext('2d')
    this.postctx = this.container
      .appendChild(document.createElement('canvas'))
      .getContext('2d')
    this.canvas = this.c.canvas
    // Generating dots
    for (let i = 0; i < this.vertexCount; i++) {
      const x = i % this.oceanWidth
      const y = 0
      const z = (i / this.oceanWidth) >> 0
      const offset = this.oceanWidth / 2
      this.vertices.push([
        (-offset + x) * this.gridSize,
        y * this.gridSize,
        z * this.gridSize,
      ])
    }
    // this.render()
  }

  render = () => {
    // console.log('render', this)
    if (!this.play) return

    const rad = (this.sin(this.frame / 100) * this.PI) / 20
    const rad2 = (this.sin(this.frame / 50) * this.PI) / 10
    this.frame++

    if (
      this.postctx.canvas.width !== this.postctx.canvas.offsetWidth ||
      this.postctx.canvas.height !== this.postctx.canvas.offsetHeight
    ) {
      this.postctx.canvas.width = this.canvas.width = this.postctx.canvas.offsetWidth
      this.postctx.canvas.height = this.canvas.height = this.postctx.canvas.offsetHeight
    }

    // this.c.fillStyle = `hsl(200deg, 100%, 2%)`
    // this.c.fillRect(0, 0, canvas.width, canvas.height)

    const grd = this.c.createRadialGradient(
      this.canvas.width / 2,
      this.canvas.height / 2,
      50,
      this.canvas.width / 2,
      this.canvas.height / 2,
      Math.max(this.canvas.width, this.canvas.height) / 2
    )
    grd.addColorStop(0, 'rgba(20,58,127,1)')
    grd.addColorStop(0.6, 'rgba(11,19,46,1)')
    grd.addColorStop(1, 'rgba(11,19,46,1)')
    this.c.fillStyle = grd
    this.c.fillRect(0, 0, this.canvas.width, this.canvas.height)
    this.c.save()
    this.c.translate(this.canvas.width / 2, this.canvas.height / 2)

    this.c.beginPath()
    this.vertices.forEach((vertex, i) => {
      // let ni = i + oceanWidth
      let x = vertex[0] - (this.frame % (this.gridSize * 2))
      let z =
        vertex[2] -
        ((this.frame * 2) % this.gridSize) +
        (i % 2 === 0 ? this.gridSize / 2 : 0)
      const wave =
        this.cos(this.frame / 45 + x / 50) -
        this.sin(this.frame / 20 + z / 50) +
        this.sin(this.frame / 30 + (z * x) / 10000)
      let y = vertex[1] + wave * this.waveSize
      const a = Math.max(0, 1 - Math.sqrt(x ** 2 + z ** 2) / this.depth)
      let tx, ty, tz

      y -= this.oceanHeight

      // Transformation variables
      tx = x
      ty = y
      tz = z

      // Rotation Y
      tx = x * this.cos(rad) + z * this.sin(rad)
      tz = -x * this.sin(rad) + z * this.cos(rad)
      x = tx
      y = ty
      z = tz

      // Rotation Z
      tx = x * this.cos(rad) - y * this.sin(rad)
      ty = x * this.sin(rad) + y * this.cos(rad)
      x = tx
      y = ty
      z = tz

      // Rotation X
      ty = y * this.cos(rad2) - z * this.sin(rad2)
      tz = y * this.sin(rad2) + z * this.cos(rad2)
      x = tx
      y = ty
      z = tz

      x /= z / this.perspective
      y /= z / this.perspective

      if (a < 0.01) return
      if (z < 0) return

      this.c.globalAlpha = a
      this.c.fillStyle = `hsl(${180 + wave * 20}deg, 100%, 75%)`
      this.c.fillRect(
        x - (a * this.vertexSize) / 2,
        y - (a * this.vertexSize) / 2,
        a * this.vertexSize,
        a * this.vertexSize
      )
      this.c.globalAlpha = 1
    })
    this.c.restore()

    // Post-processing
    this.postctx.drawImage(this.canvas, 0, 0)

    this.postctx.globalCompositeOperation = 'screen'
    this.postctx.filter = 'blur(16px)'
    // postctx.drawImage(canvas, 0, 0)
    this.postctx.filter = 'blur(0)'
    this.postctx.globalCompositeOperation = 'source-over'

    requestAnimationFrame(this.render)
  }

  playCanvas () {
    this.play = true
    this.render()
  }

  pauseCanvas () {
    this.play = false
    this.frame = 0
  }
}
