import React from 'react'
import { Input, Select, Checkbox, Button } from 'antd'
import AWS from 'aws-sdk'
import moment from 'moment'
import PerfectScrollbar from 'react-perfect-scrollbar'
import anime from 'animejs/lib/anime.es.js'
import 'react-perfect-scrollbar/dist/css/styles.css'
import 'antd/dist/antd.css'
import '../styles/sass/contact.sass'

const { Option } = Select

export default class Contact extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      isShow: false,
      msgData: {
        company: '',
        contactor: '',
        tel: '',
        email: '',
        budget: '10萬以上',
        options: [],
        source: [],
        content: ''
      },
      loading: false
    }
    this.formWrap = null
    this.formMask = null
    this.formInner = null
    this.formCloser = null
    this.formSwitch = null
    AWS.config.update({ region: 'us-east-1' })
    AWS.config.credentials = new AWS.Credentials({
      accessKeyId: 'AKIAT3TZO3E6ZUCQ5YYF',
      secretAccessKey: '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'
    })
  }

  componentDidMount() {
    document
      .querySelector('.contact-switch')
      .addEventListener('click', this.handleSwitchForm)
  }

  handleSwitchForm = () => {
    if (!this.state.isShow && !this.props.isScroll) {
      this.setState({ isShow: true }, () => {
        this.props.onDisableScroll()
        this.sceneIn()
      })
    } else if (this.state.isShow) {
      this.sceneOut()
    }
  }

  sceneIn = () => {
    this.formWrap.style.zIndex = 121
    this.formWrap.style.visibility = 'visible'
    anime
      .timeline({ easing: 'easeInSine' })
      .add({
        targets: this.formSwitch,
        opacity: [1, 0],
        scale: [1, 0.5],
        duration: 250
      })
      .add(
        {
          targets: this.formMask,
          opacity: [0, 1],
          duration: 250
        },
        '-=250'
      )
      .add(
        {
          targets: [this.formInner, this.formCloser],
          opacity: [0, 1],
          duration: 250,
          complete: () => {
            anime.remove(
              this.formSwitch,
              this.formMask,
              this.formInner,
              this.formCloser
            )
          }
        },
        '-=250'
      )
  }

  sceneOut = () => {
    anime
      .timeline({ easing: 'easeInSine' })
      .add({
        targets: this.formMask,
        opacity: [1, 0],
        duration: 250
      })
      .add(
        {
          targets: [this.formInner, this.formCloser],
          opacity: [1, 0],
          duration: 250
        },
        '-=250'
      )
      .add(
        {
          targets: this.formSwitch,
          opacity: [0, 1],
          scale: [0.5, 1],
          duration: 250,
          complete: () => {
            anime.remove(
              this.formSwitch,
              this.formMask,
              this.formInner,
              this.formCloser
            )
            this.formWrap.style.zIndex = -1
            this.formWrap.style.visibility = 'hidden'
            this.setState({ isShow: false }, () => {
              this.props.onEnableScroll()
            })
          }
        },
        '-=250'
      )
  }

  handleInputChange = (key, event) => {
    const { msgData } = this.state
    msgData[key] = event.target.value
    this.setState({ msgData })
  }

  handleSelectChange = (key, value) => {
    const { msgData } = this.state
    msgData[key] = value
    this.setState({ msgData })
  }

  handleCheckChange = (key, values) => {
    const { msgData } = this.state
    msgData[key] = values
    this.setState({ msgData })
  }

  handleSend = () => {
    const { msgData } = this.state
    if (msgData.company === '') {
      alert('請填寫您所屬的公司/單位名稱')
      return false
    } else if (msgData.contactor === '') {
      alert('請留下您的大名或稱呼')
      return false
    } else if (msgData.tel === '' && msgData.email === '') {
      alert('請留下您的聯絡電話或電子信箱')
      return false
    } else if (msgData.options.length < 1) {
      alert('請選擇至少一項諮詢項目')
      return false
    } else if (msgData.content === '') {
      alert('請留下您想諮詢的詳細需求與說明')
      return false
    }
    this.setState({ loading: true }, () => {
      const msgParams = {
        Destination: {
          ToAddresses: ['service@hystudio.tw']
        },
        Message: {
          Subject: {
            Charset: 'UTF-8',
            Data: `從官方網站寄來的訊息 - ${moment().format(
              'YYYY/MM/DD HH:mm'
            )}`
          },
          Body: {
            Html: {
              Charset: 'UTF-8',
              Data: `<strong>公司/單位名稱：</strong> ${msgData.company}<br />
              <strong>聯絡人：</strong> ${msgData.contactor}<br />
              <strong>聯絡電話：</strong> ${msgData.tel}<br />
              <strong>電子信箱：</strong> ${msgData.email}<br />
              <strong>預算需求：</strong> ${msgData.budget}<br />
              <strong>諮詢項目：</strong> ${msgData.options.join('、')}<br />
              <strong>得知管道：</strong> ${msgData.source.join(
                '、'
              )}<br /><br />
              <strong>詳細需求說明：</strong><br />
              ${msgData.content.replace(/\r?\n/g, '<br />')}`
            }
          }
        },
        Source: 'website@hystudio.tw'
      }
      const sendPromise = new AWS.SES({
        apiVersion: '2010-12-01'
      })
        .sendEmail(msgParams)
        .promise()
      sendPromise
        .then(response => {
          alert('感謝您的來信，我們將盡快與您聯繫！')
          this.setState({
            msgData: {
              company: '',
              contactor: '',
              tel: '',
              email: '',
              budget: '',
              options: [],
              source: [],
              content: ''
            },
            loading: false
          })
        })
        .catch(error => {
          alert(
            '很抱歉，您的諮詢未能成功送出，建議您可點選下方的信箱圖示直接聯繫我們'
          )
          this.setState({ loading: false })
        })
    })
  }

  render() {
    return (
      <>
        <div className='contact-form' ref={node => (this.formWrap = node)}>
          <div
            className='contact-form-mask'
            onClick={this.handleSwitchForm}
            ref={node => (this.formMask = node)}
          />
          <div
            className='contact-form-inner'
            ref={node => (this.formInner = node)}
          >
            <button
              className='close-contact-form en'
              onClick={this.handleSwitchForm}
              ref={node => (this.formCloser = node)}
            >
              <span>Close</span>
            </button>
            <div className='left en'>
              <div className='title'>CONTACT</div>
              <div className='text'>
                若您有任何網站的需求或疑問，歡迎隨時與我們聯絡，我們將提供您最專業的免費諮詢服務。
                <a href='mailto:service@hystudio.tw' title='email'>
                  <span className='icon'>
                    <svg viewBox='0 0 483.3 483.3'>
                      <path d='M424.3,57.75H59.1c-32.6,0-59.1,26.5-59.1,59.1v249.6c0,32.6,26.5,59.1,59.1,59.1h365.1c32.6,0,59.1-26.5,59.1-59.1 v-249.5C483.4,84.35,456.9,57.75,424.3,57.75z M456.4,366.45c0,17.7-14.4,32.1-32.1,32.1H59.1c-17.7,0-32.1-14.4-32.1-32.1v-249.5 c0-17.7,14.4-32.1,32.1-32.1h365.1c17.7,0,32.1,14.4,32.1,32.1v249.5H456.4z' />
                      <path d='M304.8,238.55l118.2-106c5.5-5,6-13.5,1-19.1c-5-5.5-13.5-6-19.1-1l-163,146.3l-31.8-28.4c-0.1-0.1-0.2-0.2-0.2-0.3 c-0.7-0.7-1.4-1.3-2.2-1.9L78.3,112.35c-5.6-5-14.1-4.5-19.1,1.1c-5,5.6-4.5,14.1,1.1,19.1l119.6,106.9L60.8,350.95 c-5.4,5.1-5.7,13.6-0.6,19.1c2.7,2.8,6.3,4.3,9.9,4.3c3.3,0,6.6-1.2,9.2-3.6l120.9-113.1l32.8,29.3c2.6,2.3,5.8,3.4,9,3.4 c3.2,0,6.5-1.2,9-3.5l33.7-30.2l120.2,114.2c2.6,2.5,6,3.7,9.3,3.7c3.6,0,7.1-1.4,9.8-4.2c5.1-5.4,4.9-14-0.5-19.1L304.8,238.55z' />
                    </svg>
                  </span>
                  service@hystudio.tw
                </a>
              </div>
            </div>
            <div className='right'>
              <PerfectScrollbar>
                <div className='field-board'>
                  <div className='group'>
                    <span>公司/單位</span>
                    <div className='input-wrap'>
                      <Input
                        value={this.state.msgData.company}
                        onChange={this.handleInputChange.bind(this, 'company')}
                      />
                    </div>
                  </div>
                  <div className='group'>
                    <span>聯絡人</span>
                    <div className='input-wrap'>
                      <Input
                        value={this.state.msgData.contactor}
                        onChange={this.handleInputChange.bind(
                          this,
                          'contactor'
                        )}
                      />
                    </div>
                  </div>
                  <div className='group'>
                    <span>聯絡電話</span>
                    <div className='input-wrap'>
                      <Input
                        value={this.state.msgData.tel}
                        onChange={this.handleInputChange.bind(this, 'tel')}
                      />
                    </div>
                  </div>
                  <div className='group'>
                    <span>電子信箱</span>
                    <div className='input-wrap'>
                      <Input
                        value={this.state.msgData.email}
                        onChange={this.handleInputChange.bind(this, 'email')}
                      />
                    </div>
                  </div>
                  <div className='group'>
                    <span>預算需求</span>
                    <div id='contact-select' className='select-wrap'>
                      <Select
                        value={this.state.msgData.budget}
                        onChange={this.handleSelectChange.bind(this, 'budget')}
                        getPopupContainer={() =>
                          document.getElementById('contact-select')
                        }
                      >
                        <Option key={1} value='10萬以上'>
                          10萬以上
                        </Option>
                        <Option key={2} value='5~10萬'>
                          5~10萬
                        </Option>
                        <Option key={3} value='3~5萬'>
                          3~5萬
                        </Option>
                        <Option key={4} value='3萬以內'>
                          3萬以內
                        </Option>
                      </Select>
                    </div>
                  </div>
                  <div className='group'>
                    <span>諮詢項目</span>
                    <div className='check-group'>
                      <Checkbox.Group
                        value={this.state.msgData.options}
                        onChange={this.handleCheckChange.bind(this, 'options')}
                      >
                        <Checkbox key={1} value='網站整體規劃'>
                          網站整體規劃
                        </Checkbox>
                        <Checkbox key={2} value='網站視覺設計'>
                          網站視覺設計
                        </Checkbox>
                        <Checkbox key={3} value='前端網頁製作'>
                          前端網頁製作
                        </Checkbox>
                        <Checkbox key={4} value='後端資料建置'>
                          後端資料建置
                        </Checkbox>
                        <Checkbox key={5} value='網站主機部署'>
                          網站主機部署
                        </Checkbox>
                      </Checkbox.Group>
                    </div>
                  </div>
                  <div className='group'>
                    <span>訊息來源</span>
                    <div className='check-group'>
                      <Checkbox.Group
                        value={this.state.msgData.source}
                        onChange={this.handleCheckChange.bind(this, 'source')}
                      >
                        <Checkbox key={1} value='搜尋引擎'>
                          搜尋引擎
                        </Checkbox>
                        <Checkbox key={2} value='案件報價'>
                          案件報價
                        </Checkbox>
                        <Checkbox key={3} value='名片發放'>
                          名片發放
                        </Checkbox>
                        <Checkbox key={4} value='親友介紹'>
                          親友介紹
                        </Checkbox>
                        <Checkbox key={5} value='其他管道'>
                          其他管道
                        </Checkbox>
                      </Checkbox.Group>
                    </div>
                  </div>
                  <div className='group'>
                    <span>詳細需求說明</span>
                    <div className='input-wrap'>
                      <Input.TextArea
                        value={this.state.msgData.content}
                        onChange={this.handleInputChange.bind(this, 'content')}
                        autoSize={{ minRows: 4, maxRows: 4 }}
                      />
                    </div>
                  </div>
                </div>
              </PerfectScrollbar>
              <div className='btn-box'>
                <Button onClick={this.handleSend} loading={this.state.loading}>
                  送出諮詢
                </Button>
              </div>
            </div>
          </div>
        </div>
        <button
          className={`contact-switch ${this.props.isShow ? 'show' : ''}`}
          // onClick={this.handleSwitchForm}
          ref={node => (this.formSwitch = node)}
        >
          <span>
            <svg viewBox='0 0 483.3 483.3'>
              <path d='M424.3,57.75H59.1c-32.6,0-59.1,26.5-59.1,59.1v249.6c0,32.6,26.5,59.1,59.1,59.1h365.1c32.6,0,59.1-26.5,59.1-59.1 v-249.5C483.4,84.35,456.9,57.75,424.3,57.75z M456.4,366.45c0,17.7-14.4,32.1-32.1,32.1H59.1c-17.7,0-32.1-14.4-32.1-32.1v-249.5 c0-17.7,14.4-32.1,32.1-32.1h365.1c17.7,0,32.1,14.4,32.1,32.1v249.5H456.4z' />
              <path d='M304.8,238.55l118.2-106c5.5-5,6-13.5,1-19.1c-5-5.5-13.5-6-19.1-1l-163,146.3l-31.8-28.4c-0.1-0.1-0.2-0.2-0.2-0.3 c-0.7-0.7-1.4-1.3-2.2-1.9L78.3,112.35c-5.6-5-14.1-4.5-19.1,1.1c-5,5.6-4.5,14.1,1.1,19.1l119.6,106.9L60.8,350.95 c-5.4,5.1-5.7,13.6-0.6,19.1c2.7,2.8,6.3,4.3,9.9,4.3c3.3,0,6.6-1.2,9.2-3.6l120.9-113.1l32.8,29.3c2.6,2.3,5.8,3.4,9,3.4 c3.2,0,6.5-1.2,9-3.5l33.7-30.2l120.2,114.2c2.6,2.5,6,3.7,9.3,3.7c3.6,0,7.1-1.4,9.8-4.2c5.1-5.4,4.9-14-0.5-19.1L304.8,238.55z' />
            </svg>
          </span>
        </button>
      </>
    )
  }
}
