import React from 'react'
import anime from 'animejs/lib/anime.es.js'

export default class Cursor extends React.PureComponent {
  constructor(props) {
    super(props)
    this.click = null
    this.mouseEle = null
    this.mouseEleDot = null
    this.mouseEleBor = null
    this.mouseEleBorGenerate = false
    this.x = -1
    this.y = -1
  }

  componentDidMount() {
    this.mouseEleBor.classList.add('hide')
    document.addEventListener('mousemove', this.getMouse)
    document.addEventListener('click', this.addClick)
  }

  componentDidUpdate(prevProps) {
    if (!prevProps.isShow && this.props.isShow) {
      this.mouseEleBor.classList.remove('hide')
    } else if (prevProps.isShow && !this.props.isShow) {
      this.mouseEleBor.classList.add('hide')
    }
  }

  getMouse = e => {
    this.mouseEleDot.style.left = `${e.x}px`
    this.mouseEleDot.style.top = `${e.y}px`
    if (!this.props.isShow) {
      return false
    }
    const controllBtn = [
      ...document.querySelectorAll(
        '.control-dots button, .control-dots button *'
      )
    ]
    const dotBtn = [
      ...document.querySelectorAll('.control-dots ul li, .control-dots ul li *')
    ]
    const contactBtn = [
      ...document.querySelectorAll('.contact-switch, .contact-switch *')
    ]
    const serviceBtn = document.getElementById('service-btn')
    if (contactBtn.includes(e.target)) {
      if (!this.mouseEle.classList.contains('in')) {
        this.mouseEle.classList.add('in')
        const nowTop = e.target.offsetTop + e.target.parentElement.offsetTop
        const nowLeft = e.target.offsetLeft + e.target.parentElement.offsetLeft
        anime({
          targets: this.mouseEleBor,
          width: e.target.clientWidth,
          height: e.target.clientHeight,
          left: nowLeft + e.target.clientWidth / 2,
          top: nowTop + e.target.clientHeight / 2,
          duration: 800,
          easing: 'easeOutCubic',
          complete: () => {
            if (this.mouseEle.classList.contains('in')) {
              anime.remove(this.mouseEleBor)
            }
          }
        })
      }
    } else if (dotBtn.includes(e.target)) {
      if (!this.mouseEle.classList.contains('in')) {
        anime.remove(this.mouseEleBor)
        this.mouseEle.classList.add('in')
        const nowTop =
          document.querySelector('.control-dots').offsetTop +
          e.target.offsetTop +
          e.target.parentElement.offsetTop
        const nowLeft =
          document.querySelector('.control-dots').offsetLeft +
          e.target.offsetLeft +
          e.target.parentElement.offsetLeft
        anime({
          targets: this.mouseEleBor,
          width: e.target.clientWidth,
          height: e.target.clientHeight,
          left: nowLeft + e.target.clientWidth / 2,
          top: nowTop + e.target.clientHeight / 2,
          duration: 800,
          easing: 'easeOutCubic',
          complete: () => {
            if (this.mouseEle.classList.contains('in')) {
              anime.remove(this.mouseEleBor)
            }
          }
        })
      }
    } else if (controllBtn.includes(e.target)) {
      if (!this.mouseEle.classList.contains('in')) {
        anime.remove(this.mouseEleBor)
        this.mouseEle.classList.add('in')
        const nowTop = e.target.offsetTop + e.target.parentElement.offsetTop
        const nowLeft = e.target.offsetLeft + e.target.parentElement.offsetLeft
        anime({
          targets: this.mouseEleBor,
          width: 40,
          height: 40,
          left: nowLeft + 20,
          top: nowTop + 20,
          duration: 800,
          easing: 'easeOutCubic',
          complete: () => {
            if (this.mouseEle.classList.contains('in')) {
              anime.remove(this.mouseEleBor)
            }
          }
        })
      }
    } else if (
      e.target === serviceBtn ||
      e.target.parentElement === serviceBtn
    ) {
      if (!this.mouseEle.classList.contains('in')) {
        anime.remove(this.mouseEleBor)
        this.mouseEle.classList.add('in')
        const icon = serviceBtn.querySelector('.icon')
        const nowTop = serviceBtn.offsetTop + serviceBtn.parentElement.offsetTop
        const nowLeft =
          serviceBtn.offsetLeft + serviceBtn.parentElement.offsetLeft
        anime({
          targets: this.mouseEleBor,
          width: icon.clientWidth,
          height: icon.clientHeight,
          left: nowLeft + icon.clientWidth / 2,
          top: nowTop + icon.clientHeight / 2,
          duration: 800,
          easing: 'easeOutCubic',
          complete: () => {
            if (this.mouseEle.classList.contains('in')) {
              anime.remove(this.mouseEleBor)
            }
          }
        })
      }
    } else {
      this.mouseEle.classList.remove('in')
      this.x = e.x
      this.y = e.y
      anime({
        targets: this.mouseEleBor,
        width: 30,
        height: 30,
        left: e.x,
        top: e.y,
        duration: 800,
        easing: 'easeOutCubic',
        complete: () => {
          if (
            this.x === e.x &&
            this.y === e.y &&
            !this.mouseEle.classList.contains('in')
          ) {
            anime.remove(this.mouseEleBor)
          }
        }
      })
    }
  }

  addClick = event => {
    // if (!this.props.isShow) {
    //   return false
    // }
    const clickTag = document.createElement('i')
    clickTag.setAttribute(
      'style',
      `top: ${event.y}px; left: ${event.x}px; width: 0px; height: 0px`
    )
    this.click.appendChild(clickTag)
    anime({
      targets: clickTag,
      width: [0, 50],
      height: [0, 50],
      opacity: [1, 0],
      duration: 500,
      easing: 'easeOutSine',
      complete: () => {
        anime.remove(clickTag)
        clickTag.remove()
      }
    })
  }

  render() {
    return (
      <>
        <div
          className={`cursor-element ${!this.props.isShow ? '' : ''}`}
          ref={node => (this.mouseEle = node)}
        >
          <div className='dot' ref={node => (this.mouseEleDot = node)} />
          <div className='border' ref={node => (this.mouseEleBor = node)} />
        </div>
        <div className='cursor-click' ref={node => (this.click = node)} />
      </>
    )
  }
}
