import React from 'react'
import anime from 'animejs/lib/anime.es.js'
import {
  ServiceIcon1,
  ServiceIcon2,
  ServiceIcon3,
  ServiceIcon4,
  ServiceIcon5
} from './serviceIcon'
import '../styles/sass/service.sass'

export default class Service extends React.PureComponent {
  constructor(props) {
    super(props)
    this.title = null
    this.slogan = null
    this.btn = null
    this.bg = null
    this.boxes = []
    this.anime = null
  }

  componentDidMount() {
    this.splitText()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.currentPage !== 2 && this.props.currentPage === 2) {
      this.sceneIn()
    } else if (prevProps.nextPage === 2 && this.props.nextPage !== 2) {
      this.sceneOut()
    }
  }

  splitText = () => {
    const titleStr = this.title.textContent
    const sloganStr = this.slogan.textContent
    let titleHtml = ''
    let sloganHtml = ''
    for (let i = 0; i < titleStr.length; i++) {
      titleHtml += `<span class="letter">${titleStr[i]}</span>`
    }
    for (let i = 0; i < sloganStr.length; i++) {
      sloganHtml += `<span class="letter">${sloganStr[i]}</span>`
    }
    this.title.innerHTML = titleHtml
    this.slogan.innerHTML = sloganHtml
  }

  sceneIn = () => {
    this.anime = anime
      .timeline()
      .add({
        targets: this.bg,
        translateX: [50, 0],
        opacity: [0, 0.1],
        duration: 1000,
        easing: 'easeInSine'
      })
      .add(
        {
          targets: this.title.querySelectorAll('.letter'),
          opacity: [0, 1],
          translateX: [() => `${Math.random() - 0.5 < 0 ? -20 : 20}px`, 0],
          duration: 1400,
          easing: 'easeInSine',
          delay: function(el, i) {
            return i * 100
          }
        },
        '-=800'
      )
      .add(
        {
          targets: this.slogan.querySelectorAll('.letter'),
          opacity: [0, 1],
          duration: 600,
          easing: 'easeInSine',
          delay: (e, i) => i * 40
        },
        '-=1600'
      )
      .add(
        {
          targets: this.btn,
          opacity: [0, 1],
          duration: 1000,
          easing: 'easeInSine'
        },
        '-=600'
      )
      .add(
        {
          targets: this.boxes,
          translateX: [-100, 0],
          opacity: [0, 1],
          duration: 1200,
          easing: 'easeInOutSine',
          delay: (e, i) => i * 100,
          complete: () => {
            anime.remove(
              this.bg,
              this.title.querySelectorAll('.letter'),
              this.slogan.querySelectorAll('.letter'),
              this.btn,
              this.boxes
            )
            this.anime = null
          }
        },
        '-=2200'
      )
  }

  sceneOut = () => {
    this.props.onDisableScroll()
    if (this.anime !== null) {
      this.anime.pause()
      this.anime = null
    }
    this.anime = anime
      .timeline({ easing: 'easeInOutSine' })
      .add({
        targets: this.boxes,
        translateX: -100,
        opacity: 0,
        duration: 1000
      })
      .add(
        {
          targets: [this.btn, this.slogan.querySelectorAll('.letter')],
          opacity: 0,
          duration: 1000
        },
        '-=1000'
      )
      .add(
        {
          targets: this.title.querySelectorAll('.letter'),
          opacity: 0,
          translateX: 0,
          duration: 1000
        },
        '-=1000'
      )
      .add(
        {
          targets: this.bg,
          opacity: 0,
          translateX: 50,
          duration: 1000,
          complete: () => {
            anime.remove(
              this.boxes,
              this.btn,
              this.slogan.querySelectorAll('.letter'),
              this.title.querySelectorAll('.letter'),
              this.bg
            )
            this.anime = null
            setTimeout(() => {
              this.props.onChangePage(this.props.nextPage)
            }, 100)
          }
        },
        '-=800'
      )
  }

  handleServiceBtn = () => {
    document.querySelector('.contact-switch').dispatchEvent(new Event('click'))
  }

  render() {
    return (
      <section
        id='service'
        className={`full-section ${
          this.props.currentPage === 2 ? 'current' : ''
        }`}
      >
        <div className='title-wrap'>
          <div className='slogan d-text' ref={node => (this.slogan = node)}>
            Everything You Need, Everyting We Have
          </div>
          <h2 className='title en d-text' ref={node => (this.title = node)}>
            SERVICE
          </h2>
          <button
            id='service-btn'
            className='btn'
            ref={node => (this.btn = node)}
            onClick={this.handleServiceBtn}
          >
            <span className='icon'>
              <i />
            </span>
            立即諮詢
          </button>
        </div>
        <div className='list'>
          <div className='box' ref={node => (this.boxes[0] = node)}>
            <span className='icon'>
              <ServiceIcon1 />
            </span>
            網站整體規劃
          </div>
          <div className='box' ref={node => (this.boxes[1] = node)}>
            <span className='icon'>
              <ServiceIcon2 />
            </span>
            網站視覺設計
          </div>
          <div className='box' ref={node => (this.boxes[2] = node)}>
            <span className='icon'>
              <ServiceIcon3 />
            </span>
            前端網頁製作
          </div>
          <div className='box' ref={node => (this.boxes[3] = node)}>
            <span className='icon'>
              <ServiceIcon4 />
            </span>
            後端資料庫架構
          </div>
          <div className='box' ref={node => (this.boxes[4] = node)}>
            <span className='icon'>
              <ServiceIcon5 />
            </span>
            網站部署上線
          </div>
        </div>
        <div className='bg' ref={node => (this.bg = node)}>
          <svg viewBox='-164 70.2 512 450.1'>
            <path
              fill='#ffffff'
              d='M317.1,70.2h-452.5c-15.8,0-28.6,12.8-28.6,28.6v315.5c0,15.8,12.8,28.6,28.6,28.6H27.1c0,0,8,0,8,0v22.4 c0,20.7-16.8,45.4-37.4,45.4l-54.7,0c-2.6,0-4.8,2.1-4.7,4.7c0.1,5.5,4,4.9,11.5,4.9h290.9c3.6,0,5.1-1.4,5.1-3.9 c0-4.4-5.7-5.7-11.5-5.7h-48c-20.7,0-37.4-24.8-37.4-45.4v-22.5h170.6c15.8,0,28.6-12.8,28.6-28.6V101.1 C348,84.1,334.1,70.2,317.1,70.2z M23.9,510.7c14.6-14.6,20.9-31.3,20.9-45.4v-22.5h94.4v22.5c-0.2,22.2,12.8,35.1,20.9,45.4 C160.1,510.7,23.9,510.7,23.9,510.7z M338.4,414.3c0,7.1-11.8,19-19,19h-454.9c-7.1,0-19-11.8-19-19v-64.4h492.8V414.3z M338.4,340.3h-492.8V98.6c0-7,11.7-18.7,18.7-18.7h455.1c7.1,0,19,11.8,19,19L338.4,340.3L338.4,340.3z'
            />
            <circle fill='#ffffff' cx='92' cy='391.6' r='12.7' />
          </svg>
        </div>
      </section>
    )
  }
}
