import React from 'react'
import anime from 'animejs/lib/anime.es.js'
import britz from '../images/britz.jpg'
import jenna from '../images/jenna.jpg'
import '../styles/sass/about.sass'

export default class About extends React.PureComponent {
  constructor(props) {
    super(props)
    this.title = null
    this.titleP = null
    this.dot = null
    this.line1 = null
    this.line2 = null
    this.boxes = []
    this.anime = null
  }

  componentDidMount() {
    this.splitText()
    this.generateDot()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.currentPage !== 4 && this.props.currentPage === 4) {
      this.sceneIn()
    } else if (prevProps.nextPage === 4 && this.props.nextPage !== 4) {
      this.sceneOut()
    }
  }

  splitText = () => {
    const titleStr = this.title.textContent
    let titleHtml = ''
    for (let i = 0; i < titleStr.length; i++) {
      titleHtml += `<span class="letter">${titleStr[i]}</span>`
    }
    this.title.innerHTML = titleHtml
  }

  generateDot = () => {
    for (let i = 1; i < 150; i++) {
      const dot = document.createElement('i')
      const left =
        i % 2 === 0 ? Math.ceil(i / 2) * 10 : 0 - Math.ceil(i / 2) * 10
      dot.style.transform = `translateX(${left}px)`
      this.line1.appendChild(dot)
    }
    for (let i = 1; i < 28; i++) {
      const dot = document.createElement('i')
      const top =
        i % 2 === 0 ? Math.ceil(i / 2) * 10 : 0 - Math.ceil(i / 2) * 10
      dot.style.transform = `translateY(${top}px)`
      this.line2.appendChild(dot)
    }
  }

  sceneIn = () => {
    this.anime = anime
      .timeline({ easing: 'easeInSine' })
      .add({
        targets: this.title.querySelectorAll('.letter'),
        opacity: [0, 1],
        translateX: [() => `${Math.random() - 0.5 < 0 ? -10 : 10}px`, 0],
        translateY: [0, 0],
        duration: 1400,
        delay: (el, i) => {
          return i * 100
        }
      })
      .add(
        {
          targets: this.titleP,
          translateX: [0, 0],
          translateY: [10, 0],
          opacity: [0, 1],
          duration: 800
        },
        '-=1500'
      )
      .add(
        {
          targets: this.dot,
          scale: [0, 1],
          duration: 1000
        },
        '-=800'
      )
      .add(
        {
          targets: this.line1.querySelectorAll('i'),
          opacity: [0, 1],
          scale: function(el, index, total) {
            return [0, Math.max(0, 1 - Math.floor(index / 2) * 0.0125)]
          },
          duration: 800,
          delay: function(el, i) {
            return i * 10
          }
        },
        '-=1000'
      )
      .add(
        {
          targets: this.line2.querySelectorAll('i'),
          opacity: [0, 1],
          scale: function(el, index, total) {
            return [0, 1 - Math.floor(index / 2) * 0.05]
          },
          duration: 800,
          delay: function(el, i) {
            return i * 10
          }
        },
        '-=2000'
      )
      .add(
        {
          targets: this.boxes,
          opacity: [0, 1],
          duration: 800,
          translateX: [20, 0],
          complete: () => {
            anime.remove(
              this.title.querySelectorAll('.letter'),
              this.titleP,
              this.dot,
              this.line1.querySelectorAll('i'),
              this.line2.querySelectorAll('i'),
              this.boxes
            )
            this.anime = null
          }
        },
        '-=1200'
      )
  }

  sceneOut = () => {
    this.props.onDisableScroll()
    if (this.anime !== null) {
      this.anime.pause()
      this.anime = null
    }
    this.anime = anime
      .timeline({ easing: 'easeInOutSine' })
      .add({
        targets: this.boxes,
        opacity: 0,
        translateX: -20,
        duration: 1000
      })
      .add(
        {
          targets: [
            this.dot,
            this.line1.querySelectorAll('i'),
            this.line2.querySelectorAll('i')
          ],
          scale: 0,
          duration: 1000
        },
        '-=1000'
      )
      .add(
        {
          targets: [this.title.querySelectorAll('.letter'), this.titleP],
          opacity: 0,
          translateX: 0,
          translateY: -40,
          duration: 1000,
          complete: () => {
            anime.remove(
              this.boxes,
              this.dot,
              this.line1.querySelectorAll('i'),
              this.line2.querySelectorAll('i'),
              this.title.querySelectorAll('.letter'),
              this.titleP
            )
            this.anime = null
            setTimeout(() => {
              this.props.onChangePage(this.props.nextPage)
            }, 100)
          }
        },
        '-=900'
      )
  }

  render() {
    return (
      <section
        id='about'
        className={`full-section ${
          this.props.currentPage === 4 ? 'current' : ''
        }`}
      >
        <div className='title-wrap'>
          <h2 className='tit en d-text' ref={node => (this.title = node)}>
            ABOUT US
          </h2>
          <p ref={node => (this.titleP = node)}>關於我們</p>
        </div>
        <div className='list'>
          <div className='box' ref={node => (this.boxes[0] = node)}>
            <div className='pic'>
              <img src={britz} alt='britz' />
            </div>
            <div className='content'>
              <p className='name'>BRITZ</p>
              <ul>
                <li>
                  <span>[ 鉅潞科技 ]</span> 前端研發主管
                </li>
                <li>
                  <span>[ 香港酷比 ]</span> 軟體工程師
                </li>
              </ul>
            </div>
          </div>
          <div className='box right' ref={node => (this.boxes[1] = node)}>
            <div className='pic'>
              <img src={jenna} alt='jenna' />
            </div>
            <div className='content'>
              <p className='name'>JENNA</p>
              <ul>
                <li>
                  <span>[ 一化網頁設計 ]</span> 網頁設計師
                </li>
                <li>
                  <span>[ 鉅潞科技 ]</span> 前端工程師
                </li>
              </ul>
            </div>
          </div>
          <div className='bg'>
            <span className='dot' ref={node => (this.dot = node)} />
            <span className='line line1' ref={node => (this.line1 = node)} />
            <span className='line line2' ref={node => (this.line2 = node)} />
          </div>
        </div>
        <div className='screen-line' />
      </section>
    )
  }
}
