import React from 'react'
import anime from 'animejs/lib/anime.es.js'

export default class LogoSvg extends React.PureComponent {
  constructor (props) {
    super(props)
    this.state = {
      finish: false
    }
    this.line = null
    this.text = null
    this.textBlur = null
    this.gradient1 = null
    this.gradient2 = null
  }

  componentDidMount () {
    window.addEventListener('resize', this.resetHeight)
    window.dispatchEvent(new Event('resize'))
    this.text = document.querySelectorAll('.logo-text')
    const lineObject = {
      offset: 164
    }
    const textObject = {
      opacity: 0,
      blur: 2
    }
    anime
      .timeline()
      .add({
        targets: lineObject,
        offset: 328,
        easing: 'easeOutCubic',
        delay: 1000,
        duration: 2000,
        update: () => {
          this.line.style.strokeDashoffset = `${lineObject.offset}px`
        }
      })
      .add({
        targets: textObject,
        blur: 0,
        easing: 'easeInOutSine',
        duration: 1500,
        update: () => {
          this.textBlur.setAttribute('stdDeviation', textObject.blur)
        }
      })
      .add(
        {
          targets: this.text,
          opacity: [ 0, 1 ],
          duration: 1500,
          delay: (e, i) => i * 40,
          complete: () => {
            // this.props.onChangeLogoFinish()
            anime.remove(this.line, this.textBlur, this.text)
            this.props.onFinish()
            this.setState({ finish: true })
          }
        },
        '-=1500'
      )
  }

  resetHeight = () => {
    let vh = window.innerHeight * 0.01
    document.documentElement.style.setProperty('--vh', `${vh}px`)
  }

  render () {
    return (
      <div className={`logo ${this.state.finish ? 'finished' : ''} ${this.props.currentPage > 0 ? 'left' : ''}`}>
        <svg viewBox='0 0 62.2 50'>
          <defs>
            <linearGradient
              id='g1'
              ref={(node) => (this.gradient1 = node)}
              gradientUnits='userSpaceOnUse'
              x1='0'
              y1='0'
              x2='0'
              y2='33'
              spreadMethod='reflect'
            >
              <stop offset='0' style={{ stopColor: '#E7D199' }} />
              <stop offset='0.4' style={{ stopColor: '#FFFCD3' }} />
              <stop offset='0.5' style={{ stopColor: '#EAE5BA' }} />
              <stop offset='1' style={{ stopColor: '#DFC176' }} />
            </linearGradient>
            <linearGradient
              id='g2'
              ref={(node) => (this.gradient2 = node)}
              gradientUnits='userSpaceOnUse'
              x1='0'
              y1='12'
              x2='0'
              y2='22'
              spreadMethod='reflect'
            >
              <stop offset='0' style={{ stopColor: '#E7D199' }} />
              <stop offset='0.4' style={{ stopColor: '#FFFCD3' }} />
              <stop offset='0.5' style={{ stopColor: '#EAE5BA' }} />
              <stop offset='1' style={{ stopColor: '#DFC176' }} />
            </linearGradient>
            <filter width='200' height='200' x='0' y='0' id='text-blur' filterUnits='userSpaceOnUse'>
              <feGaussianBlur ref={(node) => (this.textBlur = node)} stdDeviation='2' />
            </filter>
          </defs>
          <g>
            <path
              className='logo-line'
              ref={(node) => (this.line = node)}
              stroke='url(#g1)'
              strokeWidth='4'
              fill='none'
              d='M36.9,28.2c2.4,1.7,5.2,2.7,8.4,2.7c7.9,0,14.4-6.4,14.4-14.4S53.3,2.3,45.4,2.3S31,8.6,31,16.6 c0,8-6.4,14.4-14.4,14.4S2.3,24.6,2.3,16.6S8.6,2.3,16.6,2.3c3.3,0,6.4,1.1,8.8,3'
            />
            <polygon
              className='logo-text'
              filter='url(#text-blur)'
              fill='url(#g2)'
              points='20.1,21.4 20.1,11.7 18.2,11.7 18.2,15.6 14.3,15.6 14.3,11.7 12.4,11.7 12.4,21.4 14.3,21.4 14.3,17.5 18.2,17.5 18.2,21.4'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='url(#g2)'
              d='M41.8,11.7v2.9c0,0.4,0.1,0.9,0.2,1.3c0.1,0.4,0.3,0.8,0.6,1.1c0.3,0.3,0.6,0.6,0.9,0.9 c0.4,0.2,0.8,0.4,1.2,0.5v3h1.9v-3c0.4-0.1,0.8-0.3,1.2-0.5c0.4-0.2,0.7-0.5,0.9-0.9c0.3-0.3,0.5-0.7,0.6-1.1 c0.1-0.4,0.2-0.8,0.2-1.3v-2.9h-1.9v2.9c0,0.3-0.1,0.5-0.2,0.8c-0.1,0.2-0.2,0.4-0.4,0.6s-0.4,0.3-0.6,0.4 c-0.2,0.1-0.5,0.2-0.8,0.2c-0.3,0-0.5-0.1-0.8-0.2c-0.2-0.1-0.4-0.2-0.6-0.4s-0.3-0.4-0.4-0.6c-0.1-0.2-0.2-0.5-0.2-0.8v-2.9H41.8 z'
            />
          </g>
          <g className='logo-studio'>
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M5.6,45.7c1.6,0.1,2.8,0.1,3.3,0.1c0.1,0,0.2,0,0.3-0.1c0.1-0.1,0.1-0.2,0.1-0.3v-0.8H7.3 c-0.6,0-1.1-0.1-1.4-0.4c-0.3-0.3-0.4-0.7-0.4-1.4v-0.3c0-0.6,0.1-1.1,0.4-1.4c0.3-0.3,0.7-0.4,1.4-0.4h3.2v1.3H7.7 c-0.4,0-0.5,0.2-0.5,0.5v0.2c0,0.4,0.2,0.5,0.5,0.5h1.6c0.6,0,1,0.1,1.3,0.4c0.3,0.3,0.4,0.7,0.4,1.3v0.5c0,0.6-0.1,1-0.4,1.3 c-0.3,0.3-0.7,0.4-1.3,0.4c-0.3,0-0.6,0-1,0l-0.8,0c-0.6,0-1.2-0.1-1.9-0.1V45.7z'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M18,47h-1.6v-5h-1.9v-1.3h5.4v1.3H18V47z'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M27.5,40.7h1.6v4.6c0,0.6-0.1,1.1-0.4,1.4c-0.3,0.3-0.7,0.4-1.4,0.4h-2.1 c-0.6,0-1.1-0.1-1.4-0.4s-0.4-0.7-0.4-1.4v-4.6h1.6v4.6c0,0.4,0.2,0.5,0.5,0.5h1.4c0.4,0,0.5-0.2,0.5-0.5V40.7z'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M36.9,40.7c0.7,0,1.2,0.2,1.5,0.5c0.4,0.4,0.5,0.9,0.5,1.5v2.2c0,0.7-0.2,1.2-0.5,1.5 c-0.4,0.4-0.9,0.5-1.5,0.5h-3.5v-6.3H36.9z M37.3,42.7c0-0.5-0.3-0.8-0.8-0.8H35v3.8h1.5c0.5,0,0.8-0.3,0.8-0.8V42.7z'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M46,47h-3.2v-1.3h0.8v-3.8h-0.8v-1.3H46v1.3h-0.8v3.8H46V47z'
            />
            <path
              className='logo-text'
              filter='url(#text-blur)'
              fill='#dfc176'
              d='M56.1,45.3c0,0.6-0.1,1.1-0.4,1.4c-0.3,0.3-0.7,0.4-1.4,0.4h-2.3c-0.6,0-1.1-0.1-1.4-0.4 c-0.3-0.3-0.4-0.7-0.4-1.4v-2.9c0-0.6,0.1-1.1,0.4-1.4c0.3-0.3,0.7-0.4,1.4-0.4h2.3c0.6,0,1.1,0.1,1.4,0.4 c0.3,0.3,0.4,0.7,0.4,1.4V45.3z M54.5,42.4c0-0.4-0.2-0.5-0.5-0.5h-1.5c-0.4,0-0.5,0.2-0.5,0.5v2.9c0,0.4,0.2,0.5,0.5,0.5H54 c0.4,0,0.5-0.2,0.5-0.5V42.4z'
            />
          </g>
        </svg>
      </div>
    )
  }
}
